.App {
  padding: 30px;
  display: flex;
}

.App > div {
  margin-right: 50px;
}
.todolist {
 background-color: burlywood;
 border: 2px solid darkgray;
 border-radius: 5px;
 padding: 15px;
}


.task {
  font-weight: bold;
}
.task-done{
  text-decoration: line-through;
  opacity: 0.5;
}
.btn-filter-active{
background-color: blueviolet;
color: white;
border: 2px solid blueviolet;
border-radius: 3px;
}

.error{
  border: 2px solid red;
  outline: none;
}